import { useState } from "react";
import styles from "./Appointment.module.css";

const Appointment = () => {
    const [formData, setFormData] = useState({
        name: "",
        contact: "",
    });
    const [isModalOpen, setModalOpen] = useState(false);

    const [statusMessage, setStatusMessage] = useState(null);
    const [statusType, setStatusType] = useState(""); // "success"  "error"
    const handleModalToggle = () => {
        setModalOpen(!isModalOpen);
    };
    const sendMessageToTelegram = async (message) => {
        const BOT_TOKEN = '7720061220:AAELg2OiCYYaiRjciv3byBU9PlsbQhNreVw';
        const CHAT_ID = '1775514253';
        const TELEGRAM_API_URL = `https://api.telegram.org/bot${BOT_TOKEN}/sendMessage`;

        try {
            console.log("    Telegram...");

            const response = await fetch(TELEGRAM_API_URL, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    chat_id: CHAT_ID,
                    text: message,
                    parse_mode: 'HTML',
                }),
            });

            const responseData = await response.json();
            console.log("  Telegram API:", responseData);

            if (!response.ok || !responseData.ok) {
                throw new Error(` Telegram API: ${responseData.description}`);
            }

            console.log("   !");
            return true;
        } catch (error) {
            console.error("   :", error.message);
            return false;
        }
    };

    const validateForm = () => {
        const { name, contact } = formData;

        if (!name.trim()) {
            setStatusMessage(" ,   .");
            setStatusType("error");
            return false;
        }

        if (!contact.trim()) {
            setStatusMessage(" ,    .");
            setStatusType("error");
            return false;
        }

        if (!/^[\d\s+()-]+$/.test(contact)) {
            setStatusMessage(" ,    .");
            setStatusType("error");
            return false;
        }

        return true;
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
        setStatusMessage("");
        setStatusType("");
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!validateForm()) return;

        const { name, contact } = formData;

        const message = `
 <b>    :</b>
-  <b>:</b> ${name}
-  <b>:</b> ${contact}
-  <b>:</b> 30 
-  <b>:</b> 1500 
        `;

        setStatusMessage("  ...");
        setStatusType("loading");

        const success = await sendMessageToTelegram(message);

        if (success) {
            setStatusMessage("   !");
            setStatusType("success");
            setFormData({ name: "", contact: "" });
        } else {
            setStatusMessage("    .  .");
            setStatusType("error");
        }
    };

    return (
        <section className={styles.appointment}>
            <div className={styles.container}>

                {isModalOpen && (
                    <div className={styles.modal}>
                        <div className={styles.modalContent}>
                            <button className={styles.closeButton} onClick={handleModalToggle}>
                                &times;
                            </button>
                            <h2> </h2>
                            <ul>
                                <li> / 60 </li>
                                <li>          .</li>
                                <li>     ,  ,    
                                      .
                                </li>
                                <li>           
                                    .
                                </li>
                                <li> ,        24  -   ;
                                </li>
                                <li>       -   ,  
                                          .
                                </li>
                            </ul>
                        </div>
                    </div>
                )}
                <div>

                </div>
                <div className={styles.image}>
                    <img src="/psychologist/images/4.jpg" alt=""/>
                </div>

                <div className={styles.form}>
                    <p className={styles.therapyTrigger} onClick={handleModalToggle}>
                         
                    </p>
                    <h2 className={styles.title}>   </h2>
                    <p>  ,      </p>
                    <p className={styles.description}>
                         30 ,  1500 
                    </p>

                    {statusMessage && (
                        <div className={`${styles.statusMessage} ${styles[statusType]}`}>
                            <p>{statusMessage}</p>
                        </div>
                    )}

                    <form onSubmit={handleSubmit}>
                        <input
                            type="text"
                            name="name"
                            placeholder=" "
                            className={styles.input}
                            value={formData.name}
                            onChange={handleChange}
                        />
                        <input
                            type="tel"
                            name="contact"
                            placeholder="(+996) 555 922289"
                            className={styles.input}
                            value={formData.contact}
                            onChange={handleChange}
                        />
                        <button type="submit" className={styles.button}>
                            
                        </button>
                    </form>
                </div>
            </div>
        </section>
    );
};

export default Appointment;
