import { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import styles from "./BookingPage.module.css";

const BookingPage = () => {
    const location = useLocation();
    const [formData, setFormData] = useState({
        sessionType: "",
        format: "",
        name: "",
        contact: "",
        therapyRequest: "",
    });
    const [statusMessage, setStatusMessage] = useState(null);
    const [statusType, setStatusType] = useState(""); // "success"  "error"

    const sendMessageToTelegram = async (message) => {
        const BOT_TOKEN = "7720061220:AAELg2OiCYYaiRjciv3byBU9PlsbQhNreVw";
        const CHAT_ID = "1775514253";
        const TELEGRAM_API_URL = `https://api.telegram.org/bot${BOT_TOKEN}/sendMessage`;

        try {
            console.log("    Telegram...");

            const response = await fetch(TELEGRAM_API_URL, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    chat_id: CHAT_ID,
                    text: message,
                    parse_mode: "HTML",
                }),
            });

            const responseData = await response.json();
            console.log("  Telegram API:", responseData);

            if (!response.ok || !responseData.ok) {
                throw new Error(` Telegram API: ${responseData.description}`);
            }

            console.log("   !");
            return true;
        } catch (error) {
            console.error("   :", error.message);
            return false;
        }
    };

    useEffect(() => {
        if (location.state) {
            setFormData((prevData) => ({
                ...prevData,
                sessionType: location.state.sessionType || "",
                format: location.state.format || "",
            }));
        }
    }, [location.state]);

    const validateForm = () => {
        const { sessionType, format, name, contact } = formData;

        if (!sessionType) {
            setStatusMessage(" ,   .");
            setStatusType("error");
            return false;
        }

        if (!format) {
            setStatusMessage(" ,   .");
            setStatusType("error");
            return false;
        }

        if (!name.trim()) {
            setStatusMessage(" ,   .");
            setStatusType("error");
            return false;
        }

        if (!contact.trim()) {
            setStatusMessage(" ,    .");
            setStatusType("error");
            return false;
        }

        if (!/^[\d\s+()-]+$/.test(contact)) {
            setStatusMessage(" ,    .");
            setStatusType("error");
            return false;
        }

        return true;
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
        setStatusMessage("");
        setStatusType("");
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!validateForm()) return;

        const { sessionType, format, name, contact, therapyRequest } = formData;

        const message = `
 <b> :</b>
-  <b> :</b> ${sessionType || " "}
-  <b> :</b> ${format || " "}
-  <b>:</b> ${name}
-  <b>:</b> ${contact}
-  <b>:</b> ${therapyRequest || " "}
        `;

        setStatusMessage("  ...");
        setStatusType("loading");

        const success = await sendMessageToTelegram(message);

        if (success) {
            setStatusMessage("   !");
            setStatusType("success");
            setFormData({
                sessionType: "",
                format: "",
                name: "",
                contact: "",
                therapyRequest: "",
            });
        } else {
            setStatusMessage("    .  .");
            setStatusType("error");
        }
    };

    return (
        <div className={styles.bookingPage}>
            <div className={styles.card}>
                <h1 className={styles.title}>  </h1>

                {statusMessage && (
                    <div className={`${styles.statusMessage} ${styles[statusType]}`}>
                        <p>{statusMessage}</p>
                    </div>
                )}

                <form className={styles.form} onSubmit={handleSubmit}>
                    <label className={styles.label}>
                         
                        <select
                            name="sessionType"
                            value={formData.sessionType}
                            onChange={handleChange}
                            className={styles.input}
                        >
                            <option value="" disabled>  </option>
                            <option value="online"></option>
                            <option value="offline"></option>
                        </select>
                    </label>

                    <label className={styles.label}>
                         
                        <select
                            name="format"
                            value={formData.format}
                            onChange={handleChange}
                            className={styles.input}
                        >
                            <option value="" disabled>  </option>
                            <option value="individual"></option>
                            <option value="couple"></option>
                            <option value="group"></option>
                        </select>
                    </label>

                    <label className={styles.label}>
                         
                        <input
                            type="text"
                            name="name"
                            value={formData.name}
                            onChange={handleChange}
                            placeholder="  "
                            className={styles.input}
                        />
                    </label>

                    <label className={styles.label}>
                         
                        <input
                            type="text"
                            name="contact"
                            value={formData.contact}
                            onChange={handleChange}
                            placeholder="  "
                            className={styles.input}
                        />
                    </label>

                    <label className={styles.label}>
                          
                        <textarea
                            name="therapyRequest"
                            value={formData.therapyRequest}
                            onChange={handleChange}
                            placeholder="    "
                            className={styles.textarea}
                        ></textarea>
                    </label>

                    <button type="submit" className={styles.button}>
                         
                    </button>
                </form>
            </div>
        </div>
    );
};

export default BookingPage;
